<template>
  <div class="hello">
   {{msg}}
   <br>
   <span @click="add">add</span>
   <span @click="get">get</span>
   <JsonExcel
   class = "btn btn-default"
  :data = "json_data"
  :fields = "json_fields"
  name = ".xls"></JsonExcel>
  </div>
</template>

<script>
import {getUserList,addUserList} from '@/api/api.js'
import JsonExcel from 'vue-json-excel';
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: '',
      json_fields : {
        "name"      : "",
        "city"      : "",
        "tel"    : ""
    },
    json_data : [
        {
            "name"      : "",
            "city"      : "",
            "tel"   : "15026153526",
        },
       {
            "name"      : "",
            "city"      : "",
            "tel"   : "15026153555",
        },
        {
            "name"      : "",
            "city"      : "",
            "tel"   : "15026153526",
        }
    ],
    json_meta: [
      [{
        "key": "charset",
        "value": "utf-8"
      }]
    ],
    }
  },
  components:{
    JsonExcel
  },
  methods:{
    add(){
      addUserList({user:"111"}).then((res)=>{
        console.log(res);
      },(err)=>{
        console.log(err);
      })
    },
    get(){
      getUserList({}).then((res)=>{
        console.log(res);
      },(err)=>{
        console.log(err);
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



